import React from "react"
import {connect} from "react-redux"
import {Profile} from "./Profile.tsx"
import {getStatus, getUserProfile, savePhoto, saveProfileData, updateStatus} from "../../state/profile-reducer.ts"
import {useLocation, useNavigate, useParams} from "react-router-dom"
import {withAuthRedirect} from "../hocs/withAuthRedirect"
import {compose} from "redux"
import {AppStateType} from "../../state/redux-store"
import {ProfileType} from "../../types/types"

type MapStatePropsType ={
    profile: ProfileType,
    status: string,
    loggedUserId: any
}
type MapDispatchPropsType ={
    getUserProfile:(userId:number)=>void,
    getStatus:(userId:number)=>void,
    savePhoto: ()=>void,
    saveProfileData: ()=>void
}
type OwnPropsType ={
    router: object,
    loggedUserId:number,
}

type PropsType = MapStatePropsType & MapDispatchPropsType & OwnPropsType

class ProfileContainer extends React.Component<PropsType, AppStateType> {

    profileRefresh() {
        let userId = this.props.router.params.userId

        if (!userId) {
            userId = this.props.loggedUserId.toString()
        }
        this.props.getUserProfile(userId)
        this.props.getStatus(userId)
    }

    componentDidMount() {
        this.profileRefresh()
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if (this.props.router.params.userId !== prevProps.router.params.userId) {
            this.profileRefresh()
        }
    }

    render() {
        return (
            <Profile
                isOwner={!this.props.router.params.userId}
                props={this.props}
                savePhoto={this.props.savePhoto}
                saveProfileData={this.props.saveProfileData}
            />
        )
    }
}

function mapStateToProps(state: AppStateType):MapStatePropsType {
    return {
        profile: state.profilePage.profile,
        status: state.profilePage.status,
        loggedUserId: state.auth.userId,
    }
}

function withRouter(Component) {
    function ComponentWithRouterProp(props) {
        let location = useLocation()
        let navigate = useNavigate()
        let params = useParams()
        return (
            <Component
                {...props}
                router={{location, navigate, params}}
            />
        );
    }

    return ComponentWithRouterProp
}

export default compose(
    connect<MapDispatchPropsType, MapDispatchPropsType, OwnPropsType , AppStateType>(mapStateToProps, {getUserProfile, getStatus, updateStatus, savePhoto, saveProfileData}),
    withRouter,
    withAuthRedirect
)(ProfileContainer)