import React from "react"
import {Field, Form, Formik} from "formik"
import RoundedButton from "../../common/buttons/rounded-btn/RoundedButton"

type PropsType = {
    updateNewPostText: (text:string)=>void,
    addPost:()=>void
}

export default function AddPostForm ({updateNewPostText, addPost}: PropsType) {
    const addNewPost = (values: string) => {
        updateNewPostText( values )
        addPost()
    }

    return (
        <Formik
            initialValues={{ newPostBody: "" }}
            onSubmit={(values, {resetForm}) => {
                addNewPost( values.newPostBody )
                resetForm( {values: ''} )}}>
            {() => (
                <Form>
                    <div className="">
                        <Field
                            name={'newPostBody'}
                            as={'textarea'}
                            placeholder={'enter text'}/>
                    </div>

                    <div className="">
                        <RoundedButton logo={'Send'} type={'submit'}/>
                    </div>
                </Form>
            )}
        </Formik>
    )
}