import React from "react"
import {addPostActionCreator, updateNewPostTextActionCreator} from "../../../state/profile-reducer.ts"
import MyPosts from "./MyPosts.tsx"
import {connect} from "react-redux"
import {PostType} from "../../../types/types"
import {AppStateType} from "../../../state/redux-store"

type MapStatePropsType = {
    newPostText:string | null,
    postsData: Array<PostType>
}
type MapDispatchPropsType = {
    updateNewPostTextActionCreator: (text: string | null)=>void,
    addPostActionCreator: ()=>void
}

type PropsType = MapStatePropsType & MapDispatchPropsType

function MyPostContainer(props: PropsType) {
    return (
        <MyPosts
            props={props}
        />
    )
}

function mapStateToProps(state: AppStateType): MapStatePropsType {
    return {
        newPostText: state.profilePage.newPostText,
        postsData: state.profilePage.posts
    }
}

export default connect<MapDispatchPropsType, MapStatePropsType, AppStateType>(mapStateToProps, {updateNewPostTextActionCreator, addPostActionCreator})(React.memo(MyPostContainer))