import Post from "./posts/Post.tsx"
import React from "react"
import {MyPostPropsType} from "../../../types/types"
import AddPostForm from "./AddPostForm.tsx"

type PropsType = {
    props: MyPostPropsType
}

export default function MyPosts({ props }: PropsType) {
    return (
        <div className="posts__wrapper">
            <div>
                <h3>My post</h3>
            </div>
            <div className="post__wrapper">
                <div className="new__post__wrapper">
                    <AddPostForm updateNewPostText={props.updateNewPostTextActionCreator} addPost={props.addPostActionCreator}/>
                </div>
                <div>
                    {props.postsData.map(el => <Post key={el.id} post={el.post} like={el.likes} id={el.id}/>)}
                </div>
            </div>
        </div>
    )
}
